*{
    margin: 0;
    padding: 0;
}
body{
    background-image: url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT_xs6sVB2JCECHtP8uZNoquFWFtJC_RfjtqQ&s);
    font-family: "Bree,Serif","serif";
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
.heading{
    text-align: center;
    color: rgb(219, 91, 113);
    font-size: 50px;
}
.app{
     background: linear-gradient(#e66465, #9198e5);
     width: 95%;
     min-height: 200px;
     height: min-content;
     border-radius: 15px;
     padding: 20px;
     box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px; 
     display: flex;
     justify-content: space-evenly;
}
.answer-section{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-between;
}
.question-section{
    position: relative;
    width: 100%;
    
}
.question-count{
    color: blue;
    margin-bottom: 20px;
}
.question-text{
    color: white;
    font-size: 30px;
    font-weight: bold;

}
.answer-section button{
    width: 100%;
    color: white;
    font-size: 16px;
    background: linear-gradient(#e66465, #9198e5);
    border-radius: 15px;
    display: flex;
    padding: 10px;
    justify-content: flex-start;
    align-items: center;
    border: 2px solid rgb(55, 55, 177);
    cursor: pointer;
}
button :hover{
    background-color: rgb(41, 41, 139);
}
.scoresection{
    padding: 50px;
    font-size: 60px;
    font-weight: bold;
    color: white;
}